.shopcategory-banner {
    display: block;
    margin: 30px auto;
    max-width: 100%; /* Assurez-vous que la bannière ne dépasse pas 100% */
}

.shopcategory-indexSort {
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}

.shopcategory-sort {
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;
}

.shopcategory-sort img {
    max-width: 15px;
}

.shopcategory-indexSort p span {
    font-weight: 600;
}

.shopcategory-products {
    margin: 20px 170px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.shopcategory-products .item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    height: 100%; /* Assurez-vous que chaque élément utilise 100% de la hauteur disponible */
}

.shopcategory-products .item img {
    max-height: 200px; /* Définir une hauteur maximale pour les images */
    width: auto;
    object-fit: contain; /* Assurer un bon ajustement sans déformation */
}

.shopcategory-products .item-details {
    margin-top: 10px;
}

.shopcategory-products .item-name {
    font-size: 16px;
    font-weight: 600;
    color: #333;
}

.shopcategory-products .item-price {
    font-size: 14px;
    color: #666;
}

.shopcategory-loadmore {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background-color: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}
