/* Breadcrum.css */
.breadcrum {
    display: flex;
    align-items: center;
    font-size: 0.9em; /* Adjust the font size to make it smaller */
    color: #555; /* Professional grey color */
    margin: 10px 0; /* Adjust the margin as needed */
  }
  
  .breadcrum img {
    margin: 0 5px; /* Adjust the space between the text and the images */
    width: 10px; /* Adjust the size of the arrow icons */
    height: 10px; /* Ensure the arrow icons are square */
  }
  
  .breadcrum a {
    text-decoration: none;
    color: inherit;
  }
  
  .breadcrum a:hover {
    text-decoration: underline;
  }
  