/* brands */
/* brands */
.brands {
    margin: 50px auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.brands .row {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    justify-content: space-around;
}

.brands img {
    width: 50%;
    height: auto;
    cursor: pointer;
    filter: grayscale(100%);
}
.col-md-1{
  padding-left: 70px;
}
.col-md-2{
    padding-top: 50px;
}
.col-md-3{
    padding-top: 50px;
}


.brands img:hover {
    filter: grayscale(0);
}


  
  
  /* footer */
.footer{
  background: #000;
  color: #8a8a8a;
  font-size: 14px;
  padding: 60px 0 20px;
}
.footer p {
  color: #8a8a8a;
}
.footer h3{
  color: #fff;
  margin-bottom: 20px;
}
.footer-col-1, .footer-col-2, .footer-col-3, .footer-col-4{
  min-width: 250px;
  margin-bottom: 20px;
}
.footer-col-1 {
  flex-basis: 30%;
}
.footer-col-2 {
  flex: 1;
  text-align: center;
}
.footer-col-2 img {
  width: 180px;
  margin-bottom: 20px;
}
.footer-col-3, .footer-col-4{
  flex-basis: 12px;
  text-align: center;
}
ul {
  list-style-type: none;
}
.app-logo{
  margin-top: 20px;
}
.app-logo img {
  width: 140px;
}
.footer hr{
  border: none;
  background: #b5b5b5;
  height: 1px;
  margin: 20px 0;
}
.Copyright {
  text-align: center;
}
.row{
  display: flex;
  flex-direction: row;
  gap: 180px;
}
.row .footer-col-1{
  padding-left: 25px;
}
.row .footer-col-4{
  padding-right: 85px;
}
