.NewCollections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-top: 100px;
}
.NewCollections h1{
    color: black;
    font-size: 50px;
    font-weight: 600;
}
.NewCollections hr {
    width: 200px;
    height: 6px;
    border-radius: 6 px;
    background-color: black;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 30px;
    overflow-y: hidden; 

}
