.hero{
    height: 100vh;
    background: linear-gradient(180deg, rgb(72, 215, 248), rgb(255, 255, 255) 60%);
    display: flex;
}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
    
}
.hero-left h2{
    color: black;
    font-size: 26px;
    font-weight: 600;
    overflow-y: hidden; 

}
.hero-left p{
    color: black;
    font-size: 100px;
    font-weight: 700;    
    overflow-y: hidden; 

}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;

}
.hero-hand-icon img{
    width: 255px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: red;
    color: white;
    font-size: 22px;
    font-weight: 500;
}
.hero-latest-btn div{
    overflow-y: hidden; 

}
.hero-latest-btn img{
    width: 70px;
    height: 70px;

}
.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero-right img{
    height: 600px;
    width: 2950px;
    margin-right: 50px;
}
