
.productDisplay {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    background-color: #f9f9f9;
    box-sizing: border-box;
    width: 100%;
    position: relative; /* Add this */
}

.productdisplay-left {
    margin-left: 150px;
    width: calc(33.33% - 170px); /* Adjust width to account for margin */
    margin-right: 20px;
    box-sizing: border-box;
}

.productdisplay-titre {
    width: 33.33%;
    text-align: center;
    box-sizing: border-box;
}

.productdisplay-right {
    position: fixed;
    right: 160px;
    width: calc(33.33% - 180px);
    max-height: calc(100vh - 40px);
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    box-sizing: border-box;
    transition: top 0.3s ease;
}

/* Add this to create space for the fixed right column */
.productdisplay-right-spacer {
    width: calc(43.33% - 10px);
}

.contact-info {
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.contact-info h2 {
    margin-top: 0;
    font-size: 1.5em;
}

.contact-info p {
    margin: 10px 0;
    font-size: 1em;
}

.contact-info a {
    color: #007bff;
    text-decoration: none;
}

.contact-info a:hover {
    text-decoration: underline;
}

.productdisplay-titre h2{
    margin-left: 20px;
    margin-top: 40px;
    text-align: left;
}


.productdisplay-titre h1 {
    font-size: 2.5em; /* Adjust the size as needed */
    margin: 0;
}

.productdisplay-titre p {
    font-size: 1em; /* Adjust the size as needed */
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    max-width: 600px; /* Adjust the max-width as needed */
    text-align: left;
}


.productdisplay-main-img {
    width: 150px; /* Adjust the size as needed */
    height: 150px; /* Adjust the size as needed */
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 20px;
}

.productdisplay-right h1 {
    font-size: 1.5em; /* Adjust the size as needed */
    margin: 10px 0;
}

.productdisplay-right-description {
    margin: 10px 0;
}

.productdisplay-right-star {
    display: flex;
    align-items: center;
    justify-content: center; /* Center horizontally */
    margin: 10px 0;
}

.productdisplay-right-star img {
    width: 20px; /* Adjust the size as needed */
    height: 20px; /* Adjust the size as needed */
}

.productdisplay-right-star p {
    margin: 0 0 0 10px;
}

.productdisplay-right-prices {
    display: flex;
    align-items: baseline;
    justify-content: center; /* Center horizontally */
}

.productdisplay-right-price-old {
    text-decoration: line-through;
    margin-right: 10px;
    color: grey;
}

.productdisplay-right-price-new {
    color: rgb(49, 185, 97);
    font-size: 1.5em;
}

button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    background-color: #007bff;
    color: #ffffff;
    border: none;
    border-radius: 5px;
}

button:hover {
    background-color: #0056b3;
}
