.item {
    width: 350px;
    overflow: hidden; /* Ensure the overflow is hidden to contain the image within the specified dimensions */
  }
  
  .item img {
    width: 100%;
    height: 400px; /* Set a fixed height for the images */
    object-fit: cover; /* This will make sure the image covers the entire container while maintaining aspect ratio */
    border-radius: 8px; /* Optional: Add border-radius for rounded corners */
  }
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap:20px;
}
.item-price-new{
    color: gray;
    font-size: 18px;
    font-weight:600;
}
.item-price-old{
    color: gray;
    font-size: 18px;
    font-weight:500;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}
